import React from 'react';
import './MentorList.css';

const MentorList = ({ mentors, starredMentors, onToggleStar }) => {
  return (
    <div className="mentor-list">
      {mentors.map((mentor, index) => (
        <div key={index} className="mentor-list-item">
          <div className="list-item-header">
            <div className="mentor-info">
              <div className="name-badge-row">
                <h3 className="faculty-name">
                  {mentor.Faculty || 'Name not available'}
                </h3>
                <div className="university-badge">
                  {mentor.University || 'University not specified'}
                </div>
              </div>
            </div>
            <div className="header-buttons">
              <button 
                className={`star-button ${starredMentors.has(`${mentor.Faculty}-${mentor.University}`) ? 'starred' : ''}`}
                onClick={() => onToggleStar(`${mentor.Faculty}-${mentor.University}`)}
                type="button"
                aria-label={starredMentors.has(`${mentor.Faculty}-${mentor.University}`) ? 'Unstar mentor' : 'Star mentor'}
              >
                <svg viewBox="0 0 24 24" fill={starredMentors.has(`${mentor.Faculty}-${mentor.University}`) ? "currentColor" : "none"} xmlns="http://www.w3.org/2000/svg">
                  <path d="M12 2L15.09 8.26L22 9.27L17 14.14L18.18 21.02L12 17.77L5.82 21.02L7 14.14L2 9.27L8.91 8.26L12 2Z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
              </button>
              <a 
                href={`mailto:?subject=Inquiry about research opportunities with ${mentor.Faculty || 'Faculty Member'}&body=Dear Professor ${mentor.Faculty || 'Faculty Member'},%0D%0A%0D%0A[Email Body Here]%0D%0A%0D%0ABest regards,%0D%0A[Your Name]`}
                className="email-link"
              >
                <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  <polyline points="22,6 12,13 2,6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
                Email
              </a>
              
              {mentor.Homepage && (
                <a 
                  href={mentor.Homepage} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="homepage-link"
                >
                  <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                    <polyline points="15,3 21,3 21,9" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                    <line x1="10" y1="14" x2="21" y2="3" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  </svg>
                  Lab Website
                </a>
              )}
            </div>
          </div>
          
          <div className="list-item-content">
            {mentor['Research Interests'] && (
              <div className="info-row">
                <span className="label">Research:</span>
                <span className="value">{mentor['Research Interests']}</span>
              </div>
            )}
            
            {mentor.Positions && (
              <div className="info-row">
                <span className="label">Positions:</span>
                <span className="value positions">{mentor.Positions}</span>
              </div>
            )}
            
            {mentor.Requirements && (
              <div className="info-row">
                <span className="label">Requirements:</span>
                <span className="value requirements">{mentor.Requirements}</span>
              </div>
            )}
            
            {mentor.Notes && (
              <div className="info-row">
                <span className="label">Notes:</span>
                <span className="value notes">{mentor.Notes}</span>
              </div>
            )}
            
            {mentor['@'] && (
              <div className="info-row">
                <span className="label">Additional:</span>
                <span className="value additional">{mentor['@']}</span>
              </div>
            )}
          </div>
        </div>
      ))}
    </div>
  );
};

export default MentorList;
