import React, { useEffect, useRef, useState } from 'react';
import NET from 'vanta/dist/vanta.net.min';
import * as THREE from 'three';

const VantaBackground = ({ children }) => {
  const [vantaEffect, setVantaEffect] = useState(null);
  const [isDarkMode, setIsDarkMode] = useState(false);
  const vantaRef = useRef(null);

  // Check for dark mode
  useEffect(() => {
    const checkTheme = () => {
      const theme = document.documentElement.getAttribute('data-theme');
      setIsDarkMode(theme === 'dark');
    };

    // Check initial theme
    checkTheme();

    // Listen for theme changes
    const observer = new MutationObserver(checkTheme);
    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ['data-theme']
    });

    return () => observer.disconnect();
  }, []);

  useEffect(() => {
    if (!vantaEffect) {
      const isDark = document.documentElement.getAttribute('data-theme') === 'dark';
      setVantaEffect(
        NET({
          el: vantaRef.current,
          THREE: THREE,
          mouseControls: true,
          touchControls: true,
          gyroControls: false,
          minHeight: 200.00,
          minWidth: 200.00,
          scale: 1.00,
          scaleMobile: 1.00,
          color: isDark ? 0x06B6D4 : 0x1E40AF, // Cyan for dark mode, blue for light mode
          backgroundColor: isDark ? 0x0F172A : 0xf8fafc, // Dark background for dark mode, light for light mode
          points: 20.00,
          maxDistance: 30.00,
          spacing: 25.00
        })
      );
    }

    return () => {
      if (vantaEffect) vantaEffect.destroy();
    };
  }, [vantaEffect]);

  // Update Vanta effect when theme changes
  useEffect(() => {
    if (vantaEffect) {
      const isDark = document.documentElement.getAttribute('data-theme') === 'dark';
      vantaEffect.setOptions({
        color: isDark ? 0x06B6D4 : 0x1E40AF,
        backgroundColor: isDark ? 0x0F172A : 0xf8fafc
      });
    }
  }, [isDarkMode, vantaEffect]);

  return (
    <>
      <div ref={vantaRef} style={{ 
        position: 'fixed', 
        top: 0, 
        left: 0, 
        width: '100%', 
        height: '100%', 
        zIndex: -2 
      }} />
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        background: isDarkMode 
          ? 'linear-gradient(135deg, rgba(15, 23, 42, 0.3) 0%, rgba(30, 41, 59, 0.3) 100%)'
          : 'linear-gradient(135deg, rgba(245, 247, 250, 0.3) 0%, rgba(195, 207, 226, 0.3) 100%)',
        zIndex: -1,
        pointerEvents: 'none',
        transition: 'background 0.3s ease'
      }} />
      {children}
    </>
  );
};

export default VantaBackground;
