import React, { useState, useEffect } from 'react';
import Header from './components/Header';
import SearchBar from './components/SearchBar';
import ViewToggle from './components/ViewToggle';
import TabNavigation from './components/TabNavigation';
import MentorCard from './components/MentorCard';
import MentorList from './components/MentorList';
import StarredMentors from './components/StarredMentors';
import VantaBackground from './components/VantaBackground';
import './App.css';

function App() {
  const [mentors, setMentors] = useState([]);
  const [filteredMentors, setFilteredMentors] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [viewMode, setViewMode] = useState('cards'); // 'cards' or 'list'
  const [activeTab, setActiveTab] = useState('all'); // 'all' or 'starred'
  const [loading, setLoading] = useState(true);
  const [starredMentors, setStarredMentors] = useState(new Set());

  // Load mentors data on component mount
  useEffect(() => {
    const loadMentors = async () => {
      try {
        // Import the mentors data from the JSON file
        const mentorsData = await import('./data/mentors.json');
        setMentors(mentorsData.default);
        setFilteredMentors(mentorsData.default);
        setLoading(false);
      } catch (error) {
        console.error('Error loading mentors data:', error);
        setLoading(false);
      }
    };

    loadMentors();
  }, []);

  // Load starred mentors from localStorage on component mount
  useEffect(() => {
    const savedStarred = localStorage.getItem('starredMentors');
    if (savedStarred) {
      try {
        const starredArray = JSON.parse(savedStarred);
        setStarredMentors(new Set(starredArray));
      } catch (error) {
        console.error('Error loading starred mentors:', error);
      }
    }
  }, []);

  // Save starred mentors to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('starredMentors', JSON.stringify([...starredMentors]));
  }, [starredMentors]);

  // Filter mentors based on search term
  useEffect(() => {
    if (!searchTerm.trim()) {
      setFilteredMentors(mentors);
      return;
    }

    const filtered = mentors.filter(mentor => {
      const searchLower = searchTerm.toLowerCase();
      return (
        mentor.Faculty?.toLowerCase().includes(searchLower) ||
        mentor.University?.toLowerCase().includes(searchLower) ||
        mentor['Research Interests']?.toLowerCase().includes(searchLower) ||
        mentor.Positions?.toLowerCase().includes(searchLower)
      );
    });

    setFilteredMentors(filtered);
  }, [searchTerm, mentors]);

  const handleSearch = (term) => {
    setSearchTerm(term);
  };

  const handleViewToggle = (mode) => {
    setViewMode(mode);
  };

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  const toggleStar = (mentorId) => {
    setStarredMentors(prev => {
      const newSet = new Set(prev);
      if (newSet.has(mentorId)) {
        newSet.delete(mentorId);
      } else {
        newSet.add(mentorId);
      }
      return newSet;
    });
  };

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading mentors...</p>
      </div>
    );
  }

  return (
    <VantaBackground>
      <div className="App">
        <Header />
        <main className="main-content">
          <div className="container">
            <div className="search-section">
              <SearchBar onSearch={handleSearch} />
              <ViewToggle 
                currentView={viewMode} 
                onToggle={handleViewToggle} 
              />
            </div>
            
            <TabNavigation 
              activeTab={activeTab} 
              onTabChange={handleTabChange} 
            />
            
            {activeTab === 'all' && (
              <div className="results-info">
                <p>Showing {filteredMentors.length} of {mentors.length} mentors</p>
              </div>
            )}

            {activeTab === 'all' ? (
              <>
                {filteredMentors.length === 0 ? (
                  <div className="no-results">
                    <p>No mentors found matching your search criteria.</p>
                    <p>Try adjusting your search terms.</p>
                  </div>
                ) : (
                  <div className="mentors-container">
                    {viewMode === 'cards' ? (
                      <div className="mentors-grid">
                        {filteredMentors.map((mentor, index) => (
                          <MentorCard 
                            key={index} 
                            mentor={mentor} 
                            isStarred={starredMentors.has(`${mentor.Faculty}-${mentor.University}`)}
                            onToggleStar={() => toggleStar(`${mentor.Faculty}-${mentor.University}`)}
                          />
                        ))}
                      </div>
                    ) : (
                      <MentorList 
                        mentors={filteredMentors} 
                        starredMentors={starredMentors}
                        onToggleStar={toggleStar}
                      />
                    )}
                  </div>
                )}
              </>
            ) : (
              <StarredMentors 
                mentors={mentors}
                starredMentors={starredMentors}
                onToggleStar={toggleStar}
                viewMode={viewMode}
              />
            )}
          </div>
        </main>
      </div>
    </VantaBackground>
  );
}

export default App;
