import React, { useState, useEffect } from 'react';
import AboutModal from './AboutModal';
import ContactModal from './ContactModal';
import './Header.css';

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isSidePanelOpen, setIsSidePanelOpen] = useState(false);
  const [isAboutModalOpen, setIsAboutModalOpen] = useState(false);
  const [isContactModalOpen, setIsContactModalOpen] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 100);
    };

    // Check for saved dark mode preference
    const savedTheme = localStorage.getItem('theme');
    if (savedTheme === 'dark') {
      setIsDarkMode(true);
      document.documentElement.setAttribute('data-theme', 'dark');
    }

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleSidePanel = () => {
    setIsSidePanelOpen(!isSidePanelOpen);
  };

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  const openAboutModal = () => {
    setIsAboutModalOpen(true);
  };

  const closeAboutModal = () => {
    setIsAboutModalOpen(false);
  };

  const openContactModal = () => {
    setIsContactModalOpen(true);
  };

  const closeContactModal = () => {
    setIsContactModalOpen(false);
  };

  const openSubscribeForm = () => {
    window.open('https://docs.google.com/forms/d/e/1FAIpQLSeDVOa43GJUL_WJ_j_3gBgiDNzo5JGfHQW0QnH1TqbuoO2mcw/viewform?usp=dialog', '_blank');
  };

  const toggleDarkMode = () => {
    const newDarkMode = !isDarkMode;
    setIsDarkMode(newDarkMode);
    
    if (newDarkMode) {
      document.documentElement.setAttribute('data-theme', 'dark');
      localStorage.setItem('theme', 'dark');
    } else {
      document.documentElement.setAttribute('data-theme', 'light');
      localStorage.setItem('theme', 'light');
    }
  };

  return (
    <>
      <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
        <div className="header-content">
          <div className="logo">
            <h1>amento</h1>
            <span className="tagline">Academic Mentor Directory</span>
          </div>
                            <nav className="nav">
                    <ul>
                      <li><a href="#home" className="active">Home</a></li>
                      <li><button onClick={openAboutModal} className="nav-link-btn">About</button></li>
                      <li><button onClick={openContactModal} className="nav-link-btn">Contact</button></li>
                      <li><button onClick={openSubscribeForm} className="nav-link-btn">Subscribe</button></li>
                      <li>
                        <button onClick={toggleDarkMode} className="dark-mode-toggle" aria-label={isDarkMode ? 'Switch to light mode' : 'Switch to dark mode'}>
                          {isDarkMode ? (
                            <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <circle cx="12" cy="12" r="5" stroke="currentColor" strokeWidth="2"/>
                              <path d="M12 1V3M12 21V23M4.22 4.22L5.64 5.64M18.36 18.36L19.78 19.78M1 12H3M21 12H23M4.22 19.78L5.64 18.36M18.36 5.64L19.78 4.22" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                            </svg>
                          ) : (
                            <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79Z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                            </svg>
                          )}
                        </button>
                      </li>
                    </ul>
                  </nav>
        </div>
      </header>
      
      {/* Floating Toggle Button - Only visible when scrolled */}
      <button 
        className={`floating-toggle ${isScrolled ? 'visible' : ''}`}
        onClick={toggleSidePanel}
        aria-label="Toggle navigation menu"
      >
        <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M3 12H21M3 6H21M3 18H21" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      </button>
      
      {/* Return to Top Button - Only visible when scrolled */}
      <button 
        className={`return-to-top ${isScrolled ? 'visible' : ''}`}
        onClick={scrollToTop}
        aria-label="Return to top"
      >
        <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M12 19V5M5 12L12 5L19 12" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      </button>
      
      {/* Side Panel Navigation */}
      <div className={`side-panel ${isSidePanelOpen ? 'open' : ''}`}>
        <div className="side-panel-header">
          <h2>Navigation</h2>
          <button 
            className="close-panel"
            onClick={toggleSidePanel}
            aria-label="Close navigation menu"
          >
            <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M18 6L6 18M6 6L18 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        </div>
                    <nav className="side-panel-nav">
              <ul>
                <li><a href="#home" onClick={toggleSidePanel}>Home</a></li>
                <li><button onClick={() => { openAboutModal(); toggleSidePanel(); }} className="side-panel-link-btn">About</button></li>
                <li><button onClick={() => { openContactModal(); toggleSidePanel(); }} className="side-panel-link-btn">Contact</button></li>
                <li><button onClick={() => { openSubscribeForm(); toggleSidePanel(); }} className="side-panel-link-btn">Subscribe</button></li>
                <li>
                  <button onClick={() => { toggleDarkMode(); toggleSidePanel(); }} className="side-panel-link-btn dark-mode-toggle-side">
                    <span>{isDarkMode ? '☀️ Light Mode' : '🌙 Dark Mode'}</span>
                  </button>
                </li>
              </ul>
            </nav>
      </div>
      
      {/* Overlay for side panel */}
      {isSidePanelOpen && (
        <div className="side-panel-overlay" onClick={toggleSidePanel}></div>
      )}

      {/* About Modal */}
      <AboutModal 
        isOpen={isAboutModalOpen} 
        onClose={closeAboutModal} 
      />

      {/* Contact Modal */}
      <ContactModal 
        isOpen={isContactModalOpen} 
        onClose={closeContactModal} 
      />
    </>
  );
};

export default Header;
