import React from 'react';
import MentorCard from './MentorCard';
import MentorList from './MentorList';
import './StarredMentors.css';

const StarredMentors = ({ mentors, starredMentors, onToggleStar, viewMode }) => {
  // Filter mentors to only show starred ones
  const starredMentorsList = mentors.filter(mentor => 
    starredMentors.has(`${mentor.Faculty}-${mentor.University}`)
  );

  if (starredMentorsList.length === 0) {
    return (
      <div className="starred-mentors">
        <div className="no-starred">
          <div className="no-starred-icon">⭐</div>
          <h3>No Starred Mentors Yet</h3>
          <p>Star mentors you're interested in to see them here</p>
        </div>
      </div>
    );
  }

  return (
    <div className="starred-mentors">
      <div className="starred-header">
        <h2>Starred Mentors ({starredMentorsList.length})</h2>
        <p>Your saved mentors for easy access</p>
      </div>
      
      {viewMode === 'cards' ? (
        <div className="mentors-grid">
          {starredMentorsList.map((mentor, index) => (
            <MentorCard 
              key={index} 
              mentor={mentor} 
              isStarred={starredMentors.has(`${mentor.Faculty}-${mentor.University}`)}
              onToggleStar={() => onToggleStar(`${mentor.Faculty}-${mentor.University}`)}
            />
          ))}
        </div>
      ) : (
        <MentorList 
          mentors={starredMentorsList} 
          starredMentors={starredMentors}
          onToggleStar={onToggleStar}
        />
      )}
    </div>
  );
};

export default StarredMentors;
